
.logo { 

  width: 20px;
  
  
}
.node {
    width: 30px;
    height: 30px;
    margin-bottom: 1px;
    outline: 1px solid rgb(0, 0, 0);
    display: table-cell;
    vertical-align: middle;
  }
  
  .node-finish {
    background-image:url(D:\Users\Mucci\Desktop\AI\path-finding\src\PathFinder\circle.svg);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat; 

  }
  
  .node-start {
    background-image:url(D:\Users\Mucci\Desktop\AI\path-finding\src\PathFinder\triangletwo-right.svg);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    /* background-color: green; */
  }
  
  .node-visited {
    animation-name: visitedAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      background-color: rgba(0, 0, 66, 0.75);
      border-radius: 100%;
    }
  
    50% {
      background-color: rgba(17, 104, 217, 0.75);
    }
  
    75% {
      transform: scale(1.2);
      background-color: rgba(0, 217, 159, 0.75);
    }
  
    100% {
      transform: scale(1);
      background-color: rgba(0, 190, 218, 0.75);
    }
  }
  
  .node-wall {
    background-color: rgb(59, 62, 63);
  }
  
  .node-shortest-path {
    animation-name: shortestPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes shortestPath {
    0% {
      transform: scale(0.6);
      background-color: rgb(255, 254, 106);
    }
  
    50% {
      transform: scale(1.2);
      background-color: rgb(255, 254, 106);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(255, 254, 106);
    }
  }